  .b-section-item--big .b-section-item__desc,
  .b-section-item--bighalf .b-section-item__desc {
    display: block; } }

@keyframes lazyloadeffect {
  from {
    background-position: -400px 0; }
  to {
    background-position: 200px 0; } }
